'use strict';

const React = require('react');
const constants = require('../../constants'); 

const HomeScene = React.createClass({
	getInitialState: function() {
    return {
			selectedButtonName: 'initial' 
		};
  },

	propTypes: {
    title: React.PropTypes.string
  },

  getDefaultProps: function() {
    return {
      title: constants.LANG.EN_GB.TITLE.APP_NAME 
    };
  },

  render: function() {
		const sceneRef = this;
		const buttonNameList = ['initial', 'pending', 'applied'];
		let buttonListAlpha = [];
		for (let i = 0; i < buttonNameList.length; ++i) {
			const name = buttonNameList[i];
			const single = (
				<button
				style={{
					margin: 5,
					display: 'inline-block',
				}}
				key={'btn-alpha-' + i}
				onClick={ function(evt) {
					sceneRef.setState({
						selectedButtonName: name
					});	
				}}
				>
					{name}
				</button>
			);
			buttonListAlpha.push(single);
		}	
    return (
			<div>
				<h1>
					Welcome to {sceneRef.props.title}	
				</h1>
				<div>
					{sceneRef.props.magicNumberForEveryRouteToKnow}
				</div>
				<div
				style={{
					color: 'blue',
					fontSize: 14,
				}}
				>
					{sceneRef.state.selectedButtonName}
				</div>
				<div>
					{buttonListAlpha}
				</div>
			</div>
		);
  }
});

exports.default = HomeScene;



