require('whatwg-fetch');
require('babel-polyfill');

const constants = require('./constants');

const React = require('react');
const ReactDOM = require('react-dom');
const Router = require('react-router/lib/Router');
const Route = require('react-router/lib/Route');
const withRouter = require('react-router/lib/withRouter');
const useRouterHistory = require('react-router/lib/useRouterHistory');
const createHistory = require('history').createHistory;

const DemoFormal = React.createClass({
	render: function() {
		const sceneRef = this;
		const dict = {
			magicNumberForEveryRouteToKnow: 42
		};
    return React.cloneElement(
      sceneRef.props.children,
      dict
    );
	}
}); 

// Reference https://webpack.github.io/docs/code-splitting.html
// Reference https://github.com/ReactTraining/react-router/blob/master/docs/guides/DynamicRouting.md

/*
Note that by far ONLY string-literals could be used as the 3rd parameter to `require.ensure`, i.e. DON'T use anything like `const chunkName = "some-beautiful-name";` and put the variable `chunkName` as the 3rd parameter to `require.ensure`, OTHERWISE webpack WON'T build the bundle correctly.
*/

const testRoute = (
	<Route 
	key='TestScene'
  path={constants.ROUTE_PATHS.TEST}
	getComponent={ (nextState, callback) => {
    require.ensure([], function () {
			const scene = require("./web/scenes/TestScene").default;
      callback(null, scene);
    }, "test");
	}}
	/>
);

const mainRoutes = {
	path: constants.ROUTE_PATHS.ROOT,
	getChildRoutes(partialNextState, callback) {
		const HomeScene = require("./web/scenes/HomeScene").default;
	
		let mainRouteChildren = [
			(<Route path={constants.ROUTE_PATHS.HOME} component={withRouter(HomeScene)} key='HomeScene' />),
		];
		
		if (constants.NOT_IN_PRODUCTION) {
			// NOTE: Splitted code snippet.
			mainRouteChildren.push(testRoute);
		}

		callback(null, mainRouteChildren);
	},

	getComponent(nextState, callback) {
		callback(null, DemoFormal);
	}
};

const browserHistory = useRouterHistory(createHistory)({
  // Note that all `Route.path` properties are relative to this history `basename`
  basename: constants.ROUTE_PATHS.BASE + constants.ROUTE_PATHS.FORMAL,
});

const routes = (
	<Router history={browserHistory}>
		{mainRoutes}
	</Router>
);

ReactDOM.render(routes, document.getElementById('react-root'));



